.cards-top {
    margin: 0 -15px 26px;
    display: flex;
    .image-card {
        margin: 15px; } }

.links {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    &_main {
        margin: 30px auto; }
    &_search {
        margin: 0 auto 30px; }
    &_company {
        justify-content: flex-start;
        margin: 34px auto 38px -20px;
        @include sm {
            margin: 34px auto;
            justify-content: center; } } }

.main-link {
    display: block;
    font-weight: 700;
    color: #aaaaaa;
    padding: 9px 20px 9px;
    border-radius: 18px;
    &.is-active {
        background-color: #e5e5e5;
        color: $black; } }

.company-link {
    display: block;
    line-height: 1.2;
    font-weight: 700;
    color: #aaaaaa;
    padding: 11px 20px;
    border-radius: 20px;
    &.is-active {
        background-color: #e5e5e5;
        color: $black; } }

.to-top {
    position: fixed;
    bottom: 20px;
    right: calc(50% - 530px);
    @include md {
        right: 27px; }
    @include sm {
        display: none; } }

.cookies {
    font-size: 13px;
    line-height: 1.1;
    color: $black;
    position: fixed;
    left: calc(50% - 530px);
    bottom: 20px;
    width: 380px;
    padding: 20px 20px 14px;
    background-color: $white;
    border-radius: $radiusBig;
    box-shadow: $shadow;
    @include md {
        left: 27px; }
    @include sm {
        left: 10px;
        width: calc(100% - 20px); }
    p {
        margin-bottom: 4px; }
    &__btn {
        font-size: 13px;
        font-weight: 500;
        background: none;
        line-height: 1.5;
        margin-right: 10px;
        &_grey {
            color: $mainGrey; } } }

