.product-page {
    margin-bottom: 70px;
    &__main {
        position: relative;
        margin-top: 10px;
        margin-bottom: 66px;
        &_wright {}
        @include clr; }
    &__slider {
        float: left;
        width: 550px;
        @include md {
            width: 327px; }
        @include sm {
            float: none;
            width: 100%; } }
    &__details {
        float: left;
        width: calc(100% - 550px);
        padding-left: 40px;
        @include md {
            width: calc(100% - 327px);
            padding-left: 30px; }
        @include sm {
            float: none;
            width: 100%;
            padding-left: 0; } }
    &__close {
        display: block;
        position: absolute;
        right: -33px;
        top: 0;
        @include md {
            right: 0; } }
    &__title {
        font-size: 20px;
        line-height: 1.25;
        font-weight: 400;
        margin-bottom: 4px;
        margin-top: 36px;
        @include md {
            margin-top: 26px; } }
    &__price {
        font-weight: 700;
        font-size: 20px;
        margin-bottom: 24px;
        .rouble {
            font-size: 19px;
            font-weight: 500; } }
    &__info {
        margin-bottom: 22px;
        margin-top: 14px;
        p {
            margin-bottom: 2px; } }
    &__buttons {
        margin-top: 22px;
        font-size: 0;
        & > * {
            margin-right: 10px; } }
    &__followers {
        margin-top: 14px;
        margin-bottom: 24px; }
    &__user {
        .user {
            margin-bottom: 16px; } }
    &__cards-title {
        font-size: 20px;
        font-weight: 500;
        line-height: 1.5;
        margin-bottom: 30px;
        text-align: left; }
    &__cards {
        margin: 0 auto;
        text-align: center; } }

.views-details {
    color: $grey;
    font-size: 13px;
    line-height: 1.23; }
