.collections-page {
    &__main {
        margin-top: 9px;
        margin-bottom: 32px;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        @include sm {
            flex-flow: column; } }
    &__left {
        flex-wrap: wrap;
        flex: 0 0 470px;
        @include sm {
            flex-flow: column;
            flex: 0 0 auto; } }
    &__authors {
        position: relative;
        display: flex;
        align-items: center;
        .avatar {
            border: 2px solid $white;
            &:not(:first-of-type) {
                margin-left: -8px; } }
        .dropdown {
            top: calc(100% + 10px);
            left: -20px;
            padding: 20px; } }
    &__names {
        margin-left: 7px;
        font-weight: 500; }
    &__title {
        margin-top: 26px;
        width: 100%;
        // flex: 0 0 100%
        font-size: 40px;
        font-weight: 400;
        line-height: 1.125;
        @include sm {
            font-size: 20px;
            line-height: 1.25; } }
    &__button {
        font-size: 0;
        background: none;
        margin-left: 6px;
        & > * {
            @include icenter; }
        span {
            font-size: 15px;
            font-weight: 500;
            margin-right: 4px; }
        .icon {
            font-size: 12px;
            color: $black; } }
    &__actions {
        font-size: 0;
        & > * {
            margin-left: 9px; }
        button:first-child {
            margin-left: 0; } } }


.dropdown {
    position: absolute;
    background-color: $white;
    z-index: 100;
    border-radius: $radiusBig;
    box-shadow: $shadow;
    overflow: hidden;
    @include invis;
    &.is-open {
        @include vis; }
    &_menu {
        width: 212px; }
    &_user {
        top: calc(100% + 15px);
        right: -20px; }
    &_action {
        top: calc(100% + 15px);
        right: 24px; }
    &__title {
        font-size: 15px;
        font-weight: 500; }
    &__header,
    &__footer {
        position: relative;
        &:after {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            height: 1px;
            background-color: $light-bg; } }
    &__header {
        padding: 8px 20px;
        color: $white;
        background-color: $blue;
        &:after {
            bottom: 0; } }
    &__body {
        padding: 8px 20px; }
    &__footer {
        padding: 8px 20px 8px;
        &:after {
            top: 0; } }
    &__button {
        font-size: 15px;
        color: $black;
        font-weight: 500;
        background: none;
        line-height: 2; } }

.list {
    &__item {
        position: relative;
        &:after {
            content: '';
            position: absolute;
            left: -10px;
            right: -10px;
            top: 0;
            bottom: 0;
            border-radius: $radius;
            background-color: #f2f2f2;
            z-index: -1;
            @include invis; }
        &:hover {
            &:after {
                @include vis; } } }
    &__link {
        font-size: 15px;
        font-weight: 500;
        color: $black;
        line-height: 2; }
    &__messages-mark {
        display: block;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        @include circle(10);
        background-color: $blue; } }
