.slider {
    @include sm {
        margin-left: 10px; }
    @include invis;
    &__slide {
        width: 700px !important;
        padding-bottom: 350px;
        background-color: $light-bg;
        @include cover;
        border-radius: $radiusBig;
        @include md {
            width: 653px !important;
            padding-bottom: 326px; }
        @include sm {
            width: 285px !important; } }
    .slick-slide {
        margin: 0 12px;
        @include sm {
            margin: 0 5px; } }
    .btn-slider {
        &_prev {
            left: calc(50% - 480px); }
        &_next {
            right: calc(50% - 480px); } } }

.slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%); }


.slider-single {
    overflow: hidden;
    @include sm {
        overflow: visible; }
    &__slide {
        padding-bottom: 630px;
        @include cover;
        border-radius: $radiusBig;
        @include md {
            padding-bottom: 374px; }
        @include sm {
            width: 270px;
            padding-bottom: 310px; } }
    .slick-slide {
        @include sm {
            margin: 0 6px; } }
    .btn-single-slider {
        &_prev {
            left: 20px;
            transition: visibility 0.1s;
            &.is-hidden {
                visibility: hidden; } }
        &_next {
            right: 20px;
            transition: visibility 0.1s;
            &.is-hidden {
                visibility: hidden; } } } }

