.out {
	position: relative;
	overflow: hidden; }

.container-inner {
	position: relative;
	margin: 0 auto;
	padding: 0 27px;
	max-width: 1014px;
	@include clr;
	@include sm {
		padding: 0 10px; } }

.container-narrow {
	max-width: 654px;
	margin: 0 auto;
	padding: 0 27px;
	@include md {
		max-width: 1014px;
		padding: 0 70px; }
	@include sm {
		padding: 0 10px; } }

.tablet-hide {
	@include md {
		display: none; } }

.mob-hide {
	@include sm {
		display: none; } }

.mob-block {
	display: none;
	&.btn,
	&.btn-circle {
		display: none; }
	@include sm {
		display: block;
		&.btn,
		&.btn-circle {
			display: inline-block; } } }





