.header {
	padding: 30px 0;
	@include sm {
		padding: 10px 0 20px; }
	&__container {
		@include flex-center;
		flex-wrap: wrap; }
	&__search {
		position: relative;
		flex-grow: 1;
		margin: 0 26px;
		@include sm {
			order: 4;
			flex: 0 0 100%;
			margin: 12px 0 0; } }
	&__user {
		margin-left: 10px;
		@include cover;
		@include sm {
			order: 3; } }
	&__action {
		@include sm {
			order: 1; } }
	&__logo {
		@include sm {
			order: 2;
			flex-grow: 1;
			text-align: center; } } }

.main-search {
	position: relative;
	font-size: 0;
	&__input {
		width: 100%;
		font-size: 15px;
		font-family: $helv;
		color: $black;
		display: inline-block;
		vertical-align: middle;
		font-weight: 500;
		background-color: $light-bg;
		height: 36px;
		border-radius: $radius;
		padding-left: 38px;
		&::placeholder {
			font-family: $helv;
			color: $grey;
			font-weight: 500; }
		&:focus {
			&::placeholder {
				font-size: 0; } } }
	&__icon {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 12px;
		font-size: 0;
		background-color: transparent;
		color: #b7b7b7;
		.icon {
			font-size: 16px;
			line-height: 1; } } }


.logo {
	display: inline-block;
	vertical-align: middle;
	height: 24px;
	width: 68px;
	&_big {
		height: 30px;
		width: 90px;
		display: inline-block; } }
