.modal__title {
  font-size: 20px;
  color: #333333;
  font-weight: 500;
  margin-bottom: 32px;
  font-family: "HelveticaNeue", Arial, Helvetica, sans-serif;
}

.modal__container_complaints .modal__title {
  margin-bottom: 20px;
}

.modal__buttons {
  display: flex;
  flex-flow: column;
  align-items: center;
}

.card-small {
  padding: 15px;
  border-radius: 8px;
  background-color: #fff;
}

.card-outline {
  border: 1px solid #e2e2e2;
  border-radius: 5px;
  text-align: left;
}

.card-outline__header {
  line-height: 1.25;
  padding: 10px 15px;
  border-bottom: 1px solid #e2e2e2;
}

.card-outline__body {
  padding: 15px;
}

.image-card {
  display: block;
  width: 300px;
  height: 350px;
  padding: 25px 20px 20px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  border-radius: 8px;
  position: relative;
  color: #fff;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

.image-card__type {
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 4px;
}

.image-card__title {
  font-size: 25px;
  font-weight: 500;
  line-height: 1.2;
  letter-spacing: 0.02em;
}

.image-card__title_single {
  margin-top: -7px;
}

.image-card__author {
  font-size: 0;
}

.image-card__avatar {
  display: inline-block;
  vertical-align: middle;
}

.image-card__name {
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
  font-size: 15px;
  font-weight: 500;
}

.card {
  display: block;
  width: 222px;
  margin-bottom: 28px;
  text-align: left;
}

@media only screen and (max-width: 767px) {
  .card {
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 560px) {
  .card {
    width: 145px;
  }
}

.card__image {
  max-width: 100%;
  display: block;
  border-radius: 8px;
  margin-bottom: 8px;
}

.card__descr {
  position: relative;
  font-size: 13px;
  line-height: 1;
  color: #8e8e8e;
  font-weight: 500;
  margin-bottom: 2px;
}

.card__product_special {
  color: #333333;
}

.card__price {
  font-weight: 500;
}

.card__special {
  position: absolute;
  right: 0;
  top: 0;
}

.card__special .icon {
  color: #0078ff;
  font-size: 16px;
}

.card .rouble {
  font-weight: 500;
  font-size: 14px;
}

.cards-wrapper {
  text-align: center;
}

.gutter-sizer {
  width: 24px;
}

@media only screen and (max-width: 767px) {
  .gutter-sizer {
    width: 20px;
  }
}

@media only screen and (max-width: 560px) {
  .gutter-sizer {
    width: 10px;
  }
}

.grid {
  margin: 0 auto;
  display: inline-block;
  width: 960px;
}

@media only screen and (max-width: 1023px) {
  .grid {
    width: 714px;
  }
}

@media only screen and (max-width: 767px) {
  .grid {
    width: 464px;
  }
}

@media only screen and (max-width: 560px) {
  .grid {
    width: 300px;
  }
}

.slider {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s, visibility 0.5s;
}

@media only screen and (max-width: 767px) {
  .slider {
    margin-left: 10px;
  }
}

.slider__slide {
  width: 700px !important;
  padding-bottom: 350px;
  background-color: #eeeeee;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  border-radius: 8px;
}

@media only screen and (max-width: 1023px) {
  .slider__slide {
    width: 653px !important;
    padding-bottom: 326px;
  }
}

@media only screen and (max-width: 767px) {
  .slider__slide {
    width: 285px !important;
  }
}

.slider .slick-slide {
  margin: 0 12px;
}

@media only screen and (max-width: 767px) {
  .slider .slick-slide {
    margin: 0 5px;
  }
}

.slider .btn-slider_prev {
  left: calc(50% - 480px);
}

.slider .btn-slider_next {
  right: calc(50% - 480px);
}

.slick-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slider-single {
  overflow: hidden;
}

@media only screen and (max-width: 767px) {
  .slider-single {
    overflow: visible;
  }
}

.slider-single__slide {
  padding-bottom: 630px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  border-radius: 8px;
}

@media only screen and (max-width: 1023px) {
  .slider-single__slide {
    padding-bottom: 374px;
  }
}

@media only screen and (max-width: 767px) {
  .slider-single__slide {
    width: 270px;
    padding-bottom: 310px;
  }
}

@media only screen and (max-width: 767px) {
  .slider-single .slick-slide {
    margin: 0 6px;
  }
}

.slider-single .btn-single-slider_prev {
  left: 20px;
  transition: visibility 0.1s;
}

.slider-single .btn-single-slider_prev.is-hidden {
  visibility: hidden;
}

.slider-single .btn-single-slider_next {
  right: 20px;
  transition: visibility 0.1s;
}

.slider-single .btn-single-slider_next.is-hidden {
  visibility: hidden;
}

.collection {
  display: block;
  position: relative;
  width: 300px;
}

.collection__images {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 200px;
  margin-bottom: 8px;
}

.collection__image {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  border-radius: 8px;
  background-color: #eeeeee;
}

.collection__image_main {
  width: 195px;
  padding-bottom: 200px;
  margin-right: 5px;
}

.collection__image_secondary {
  padding-bottom: 97px;
  width: 100px;
}

.collection__image_secondary:last-of-type {
  margin-top: 6px;
}

.collection__title {
  font-weight: 500;
  line-height: 1.1;
  letter-spacing: 0.02em;
  margin-top: 4px;
}

.collection__private {
  font-size: 0;
  position: absolute;
  bottom: -2px;
  right: 0;
}

.text-blue {
  color: #0078ff;
}

.text-tag {
  color: #0078ff;
}

.text-muted {
  color: #8e8e8e;
}

.text-regular {
  font-weight: 400;
}

.text-medium {
  font-weight: 600;
}

.text-small {
  font-size: 13px;
}

.text-small-muted {
  font-size: 13px;
  color: #8e8e8e;
  line-height: 1.23;
}

.rouble {
  font-family: "PTRouble", "HelveticaNeueCyr", Arial, Helvetica, sans-serif;
}
